<template>
  <view class="uni_back_nav">
    <button class="btn" @click="toBack" size="mini" plain hover-class="hover_flag">
      <text class="iconfont icon-back"></text>
    </button>
    <slot></slot>
  </view>
</template>

<script>
export default {
  methods: {
    toBack() {
      uni.navigateBack({
        delta: 1
      })
    },
  }
}
</script>

<style scoped lang="scss">
  .uni_back_nav{
    @include wh(100%, 14%);
    /* #ifndef MP-WEIXIN */
    background-color: $bgc-black2;
    /* #endif */
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;

    .btn{
      @include wh(120rpx, 120rpx);
      float: left;
      margin: 80rpx 0 0 20rpx;
      border: none;
      border-radius: 50%;
      .icon-back{
        color: $font-color-white;
        font-size: 50rpx;
        font-weight: bold;
      }
    }
    .hover_flag{
      @include hover();
    }

  }
</style>